@font-face {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  font-display: swap;
  src: local("Opens Sans"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
a {
  text-decoration: none;
}
.site-list,
.user-list,
.promo__list,
.info__list,
.reviews__list,
.social,
.catalog__list,
.order__group-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  box-sizing: border-box;
  position: relative;
  width: 81.25%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 87%;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1050px;
  }
}
.page {
  height: 100%;
}
.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
}
.page__body-title {
  position: relative;
  padding: 74px 0;
  background-color: #f1f1f1;
  text-align: center;
}
@media (min-width: 768px) {
  .page__body-title {
    padding-top: 101px;
    padding-bottom: 75px;
  }
}
@media (min-width: 1150px) {
  .page__body-title {
    padding-bottom: 96px;
  }
}
.section-title {
  margin: 0;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .section-title {
    font-size: 53px;
    line-height: 57px;
  }
}
.button {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  padding: 14px 14px;
  font-size: 18px;
  line-height: 20px;
  font-weight: bold;
  color: #444444;
  text-transform: uppercase;
  text-align: center;
  border: 2px solid #444444;
  background-color: transparent;
}
.button:hover,
.button:focus {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.page-header {
  box-sizing: border-box;
  min-height: 79px;
  background-color: #ffffff;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .page-header {
    min-height: 88px;
  }
}
@media (min-width: 1150px) {
  .page-header {
    padding: 0;
  }
}
.page-header .container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 22px;
}
@media (min-width: 768px) {
  .page-header .container {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 22px;
    padding-bottom: 20px;
  }
}
@media (min-width: 1150px) {
