    min-height: 88px;
  }
}
.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.6;
}
.page-header__logo:active {
  opacity: 0.3;
}
@media (min-width: 1150px) {
  .page-header__logo {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 149px;
    height: 72px;
  }
}
.page-header__logo-img {
  display: block;
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) {
  .page-header__logo-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .page-header__logo-img {
    width: 149px;
    height: 72px;
  }
}
.page-header__info {
  position: absolute;
  right: 0;
  bottom: -44px;
  margin: 0;
  font-size: 15px;
  color: #444444;
}
@media (max-width: 767px) {
