}
.menu {
  font-weight: bold;
}
@media (min-width: 320px) and (max-width: 767px) {
  .menu {
    margin-top: 22px;
  }
}
.menu--opened .menu__toggle::before,
.menu--opened .menu__toggle::after {
  top: 14px;
  width: 24px;
}
.menu--opened .menu__toggle::before {
  transform: rotate(45deg);
}
.menu--opened .menu__toggle::after {
  transform: rotate(-45deg);
  box-shadow: none;
}
@media (max-width: 767px) {
  .menu--opened .menu__list {
    display: block;
  }
}
.menu__toggle {
  position: absolute;
  top: 22px;
  right: -5px;
  display: block;
  width: 30px;
  height: 30px;
  padding: 0;
  background-color: transparent;
  border: none;
}
.menu__toggle::before,
.menu__toggle::after {
  content: "";
  position: absolute;
  left: 5px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
}
.menu__toggle::before {
  top: 8px;
}
.menu__toggle::after {
  top: 14px;
  box-shadow: 0 6px 0 0 #231f20;
}
@media (min-width: 768px) {
  .menu__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .menu__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .site-list {
    position: absolute;
    z-index: 10;
    bottom: -44px;
    left: 0;
    display: grid;
    grid-template-columns: max-content max-content;
    column-gap: 35px;
    row-gap: 50px;
    max-width: 400px;
  }
}
@media (min-width: 1150px) {
  .site-list {
    top: 32px;
    left: 0;
    bottom: auto;
    column-gap: 60px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .site-list__item::before {
    content: "";
    position: absolute;
    left: calc(18.75vw / -2);
    width: 100vw;
    height: 1px;
    background-color: #ececec;
  }
}
@media (min-width: 320px) and (max-width: 1149px) {
  .site-list__item--extra {
    display: none;
  }
}
@media (min-width: 1150px) {
  .site-list__item:first-child {
    position: relative;
  }
  .site-list__item:first-child::after {
    content: "";
    position: absolute;
    top: -32px;
    left: 115%;
    bottom: -32px;
    width: 1px;
    background-color: #ececec;
  }
}
.site-list__link {
  position: relative;
  display: block;
  color: #000000;
}
.site-list__link:hover,
.site-list__link:focus {
  opacity: 0.6;
}
.site-list__link:active {
  opacity: 0.3;
}
@media (min-width: 320px) and (max-width: 767px) {
  .site-list__link {
    padding: 28px 0;
  }
}
@media (min-width: 768px) {
  .site-list__link--extra {
    font-size: 15px;
  }
}
@media (min-width: 768px) {
  .user-list {
    display: flex;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .user-list__item::before {
    content: "";
    position: absolute;
    left: calc(18.75vw / -2);
    width: 100vw;
    height: 1px;
    background-color: #ececec;
  }
}
@media (min-width: 768px) {
  .user-list__item::before,
  .user-list__item::after {
    content: "";
